import {Component, ViewChild, OnInit, AfterViewInit} from '@angular/core';
import {Platform} from '@ionic/angular';
import {Renderer2} from '@angular/core';

import {DeviceMotion, DeviceMotionAccelerationData } from '@ionic-native/device-motion/ngx';
@Component({
  selector: 'app-canvas-draw',
  templateUrl: './canvas-draw.page.html',
  styleUrls: ['./canvas-draw.page.scss'],
})
export class CanvasDrawPage implements OnInit, AfterViewInit {
  @ViewChild('myCanvas') canvas: any;
  x = 1;
  y = 1;
  z = 0;
  canvasElement: any;
  constructor(public platform: Platform, public renderer: Renderer2, private deviceMotion: DeviceMotion) {

  }

  ngOnInit() {
      this.canvasElement = this.canvas.nativeElement;

      this.renderer.setAttribute(this.canvasElement, 'width', this.platform.width() + '');
      this.renderer.setAttribute(this.canvasElement, 'height', this.platform.height() + '');
      window.addEventListener('compassneedscalibration', function(event) {
          event.preventDefault();
      }, true);
      window.addEventListener('devicemotion', this.processEvent, true);
      this.handleDrawing();

  }
  ngAfterViewInit() {
      this.deviceMotion.getCurrentAcceleration().then(
          (acceleration: DeviceMotionAccelerationData) => console.log(acceleration),
          (error: any) => console.log(error)
      );

// Watch device acceleration
      const subscription = this.deviceMotion.watchAcceleration({frequency: 1000 }).subscribe((acceleration: DeviceMotionAccelerationData) => {
          console.log(acceleration);
          this.x = acceleration.x;
          this.y = acceleration.y;
          this.z = acceleration.z;
          this.handleDrawing();
      });
  }
  handleDrawing() {
    const ctx = this.canvasElement.getContext('2d');
    console.log(this.x);
    console.log(this.y);
    const x_change = 100 * this. x;
    const y_change = this.y * 2;
    ctx.fillStyle = 'yellow';
    ctx.fillRect(0, 0, this.canvasElement.width, this.canvasElement.height);
    let i;
    const amplitude = 90;
    const width = this.canvasElement.width;
    const step = 1;
    const frequency = y_change * 4;


    ctx.moveTo(0, this.canvasElement.height / 2);
    ctx.lineTo(width * 100, this.canvasElement.height / 2);
    ctx.strokeStyle = 'black';
    ctx.lineWidth = '1';

    ctx.stroke();
    ctx.save();
    if (this.x > 1) {
      ctx.translate(-x_change * 100, 0);
    }
console.log('hi');
    ctx.beginPath();
    ctx.moveTo(0, this.canvasElement.height / 2);
    const j = this.canvasElement.height / 2;

    const c = width / Math.PI / (frequency * 2);
    for (i = 0; i < width * 1000; i += step) {
      const k = amplitude * Math.sin(i / c);
      ctx.lineTo(i, j + k);

    }
    ctx.strokeStyle = '#0096FF';
    ctx.lineWidth = '5';

    ctx.stroke();

    ctx.restore();


  }
  processEvent(event) {
      this.x = event.accelerationIncludingGravity.x;
      this.y = event.accelerationIncludingGravity.y;
      this.z = event.accelerationIncludingGravity.z;
      this.handleDrawing();
  }


}
